.node {
    width: 28px;
    height: 28px;
    background-color: #eeeeee;
    border: 1px solid #b9b9b9;
  }

html, body {
    margin: 0;
    padding: 0;
}

.start-node {
    background-color: #B5D99C;
}

.end-node {
    background-color: #C2211E;
}

.weighted-node {
  background-color: burlywood;
}

.visited-node {
  animation-name: visitedAnimation;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.visited-node-in-que {
  animation-name: visitedInQueAnimation;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.solved-node {
    animation-name: solvedAnimation;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
}

.wall-node {
    animation-name: wallAnimation;
    animation-duration: 0.25s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
}


.solve-maze-button {
    margin: 0;
    padding: 0;
    border: 0
}

@keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: #E5D9FC;
    }
  
    50% {
      background-color: #B28EF6;
    }
  
    75% {
      transform: scale(1.2);
      background-color: #7F42F0;
    }
  
    100% {
      transform: scale(1);
      background-color: #5411D0;
    }
}

@keyframes solvedAnimation {
    0% {
      transform: scale(0.3);
      background-color: #FFFF82;
    }
  
    50% {
      background-color: #FFFF99;
    }
  
    75% {
      transform: scale(1.2);
      background-color: #FFFF47;
    }
  
    100% {
      transform: scale(1);
      background-color: #FFFF82;
    }
}

@keyframes visitedInQueAnimation {
  0% {
    transform: scale(0.3);
    background-color: #D4C4E9;
  }

  50% {
    background-color: #BEA6DD;
  }

  75% {
    transform: scale(1.2);
    background-color: #B397D8;
  }

  100% {
    transform: scale(1);
    background-color: #885CC1;
  }
}

@keyframes wallAnimation {
    0% {
      transform: scale(0.3);
      background-color: #FED8C3;
    }
  
    50% {
      background-color: #FDA372;
    }
  
    75% {
      transform: scale(1.2);
      background-color: #FC6E22;
    }
  
    100% {
      transform: scale(1);
      background-color: #FB6107;
    }
}